.explore{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
.explore .publish{
    height: 60px;
    text-align: center;
    width: 500px;
    max-width: 80vw;
    border-radius: 10px;
    margin: 20px 0px;
    font-size: 1.8rem;
    background-color: aquamarine;
    text-decoration: none;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(18, 18, 18);
}
.explore .idea_box .author{
    display: flex;
    background-color: aqua;
    border-radius: 15px 15px 0px 0px;
    padding: 10px;
}
.explore .idea_box .author .dp{
    height: 40px;
    width: 40px;
    border-radius: 20px;
    background-color: gray;
}
.explore .idea_box .author .name{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0px 15px;
}
.explore .idea_box .author .connect{
    border: 1px solid black;
    background-color: rgb(70, 70, 222);
    color: aliceblue;
    padding: 5px 15px;
    border-radius: 5px;
    text-decoration: none;
    margin-left: auto;
}
.idea_box{
    display: flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    width: 80vw;
    margin: 15px;
    background-color: rgb(162, 228, 228);
    border-radius: 15px;
}
.idea{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 15px;
}
.explore .content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.explore .idea img{
    background-color: #8c8a8a;
    padding: 10px;
    margin-right: 30px;
    width: 20%;
}
.explore .idea .title{
    margin: 2px;
    padding: 5px;
    font-size: 2rem;
    font-family: revert;
    font-weight: 600;
    width: 100%;
    text-align: left;
}
.explore .idea .desc{
    padding: 15px;
    padding-left: 25px;
    font-size: 1.1rem;
}
.explore .comments{
    padding: 10px;
    background-color: aliceblue;
    margin-top: 15px;
    border-radius: 3px;
    font-size: 1.3rem;
    color: rgb(50, 49, 49);
    margin: 15px;
}