.nav{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    background: #FFFFFF;
    border-bottom: 1px solid #000000;
    position: sticky;
    top: 0px;
    width: 100%;
  }
  .nav .title{
    width: 20vw;
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .nav .logo{
    height: 57px;
    width: 57px;
    background-color: grey;
    border-radius: 30px;
    margin: 16px 22px;
  }
  .nav .name{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 37px;
    line-height: 45px;
  }
  .nav .navigations{
    width: 31vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
  .nav .navigations a{
    font-size: 1.5rem;
    transition: 0.4s all;
  }
  .nav .navigations a:hover{
    scale: 1.2;
  }
  .nav a{
    text-decoration: none;
    color: black;
  }
  .nav .sign{
    width: 20vw;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: end;
  }
  .nav .sign a{
    width: 111px;
    height: 45px;
    border-radius: 14px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    transition: 0.4s all;
  }
  .nav .sign a:hover{
    scale: 1.1;
  }
  .nav .signin{
    display: flex;
    align-items: center;
    justify-content: center;
    background: #64FC7C;
    margin-right: 30px;
  }
  .nav .signup{
    display: flex;
    align-items: center;
    justify-content: center;
    background: #4CE4D2;
    margin-right: 20px;
  }